@import url(https://fonts.googleapis.com/css?family=Poiret+One);

@import url(https://cdnjs.cloudflare.com/ajax/libs/weather-icons/2.0.9/css/weather-icons.min.css);

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --rad: 0.7rem;
  --dur: 0.3s;
  --color-dark: #2f2f2f;
  --color-light: #fff;
  --color-brand: #57bd84;
  --font-fam: "Lato", sans-serif;
  --height: 5rem;
  --btn-width: 6rem;
  --bez: cubic-bezier(0, 0, 0.43, 1.49);
}

body {
  background-color: #f5f5f5;
  background: var(--color-dark);
  height: 100vh;
  height: 100vh;
}
.container{
    padding: 40px 0;
    height: 100%;
    width: 80%;
    max-width: 1280px;
    margin: auto;
}
.widget {
    background: rgba(0, 0, 0, 0.425);
    margin: auto;
  display: flex;
  height: 400px;
  width: 680px;
  flex-wrap: wrap;
  /* cursor: pointer; */
  border-radius: 20px;
  box-shadow: 0 27px 55px 0 rgba(0, 0, 0, 0.63),
    0 17px 17px 0 rgba(0, 0, 0, 0.774);
    transition: transform 0.5s ease-in-out;
}

.widget:hover{
    transform: translateY(-10px);
}
.widget .weatherIcon {
  flex: 1 100%;
  height: 50%;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  /* background: #fafafa; */
  font-family: weathericons;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.wi.u{
    color: #f0c420;
    font-size: 130px;
}
.widget .weatherInfo {
  flex: 0 0 70%;
  height: 40%;
  background: #080705;
  display: flex;
  align-items: center;
  color: white;
}
.widget .weatherInfo .temperature {
  flex: 0 0 40%;
  width: 100%;
  font-size: 60px;
  display: flex;
  justify-content: space-around;
  margin-right: 20px;
}
.widget .weatherInfo .description {
  flex: 0 60%;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  justify-content: center;
}
.widget .weatherInfo .description .weatherCondition {
  text-transform: uppercase;
  font-size: 30px;
  font-weight: 100;
}
.widget .weatherInfo .description .place {
  font-size: 15px;
}
.widget .date {
  flex: 0 0 30%;
  height: 40%;
  background: #70c1b3;
  display: flex;
  justify-content: space-around;
  align-items: center;
  color: white;
  font-size: 30px;
  font-weight: 800;
  text-align: center;
}

.widget .extra-temp {
  flex: 1 100%;
  padding: 5% 0;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  background: #fafafa;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  font-size: 100px;
}

.widget .extra-temp p {
  font-size: 25px;
  font-weight: 600;
}

.temp-info-minmax,
.weather-extra-info,
.two-sided-section {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  text-align: center;
  align-items: center;
  color: #70c1b3;
  /* background-color: blanchedalmond; */
}

.widget .extra-temp .extra-info-leftside {
  text-align: left;
  font-weight: 600;
  text-transform: capitalize;
  color: #080705;
  font-size: 15px;
}

/* input search button style  */
.wrap {
    width: 100%;
    display: flex;
    justify-content: center;
    text-align: center;
    margin: auto;
    margin-bottom: 50px;
  }


.search {
  width: 90%;
  display: flex;
  justify-content: center;
  
}

.searchTerm {
  width: 80%;
  /* border: 3px solid #00b4cc; */
  border: none;
  padding: 16px;
  height: 50px;
  border-radius: 20px 20px 20px 20px;
  outline: none;
  font-size: 18px;
  box-shadow: 0 27px 55px 0 rgba(0, 0, 0, 0.63),
  0 17px 17px 0 rgba(0, 0, 0, 0.774);
  transition: all 0.5s ease-in-out;
}

.searchTerm:focus {
  color: #00b4cc;
}

.searchButton {
  width: 120px;
  height: 50px;
  border: none;
  background: #048b9c;
  text-align: center;
  color: #fff;
  border-radius: 0 20px 20px 0;
  cursor: pointer;
  font-size: 20px;
  outline: none;
  font-size: 16px;
  display: none;
}

.searchButton:hover {
  background-color: #17b8ce;
}



.noselect {
    -webkit-touch-callout: none; /* iOS Safari */
      -webkit-user-select: none; /* Safari */
       -khtml-user-select: none; /* Konqueror HTML */
         -moz-user-select: none; /* Old versions of Firefox */
          -ms-user-select: none; /* Internet Explorer/Edge */
              user-select: none; /* Non-prefixed version, currently
                                    supported by Chrome, Edge, Opera and Firefox */
  }

/*Resize the wrap to see the search bar change!*/
/* .wrap {
  width: 30%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
} */

@media (max-width:768px){
    .wrap{
        width: 100%;
    }
    .wrap .search{
        width: 100%;
    }
    .searchTerm{
        width: 90%;
    }
    .widget{
        width: 90%;
        height: auto;
        margin: auto;
        flex-direction: column;
    }
    .wi.u{
        font-size: 70px;
        padding: 20px;
    }
    .widget .weatherInfo .description{
        margin: auto;
    }
    .widget .weatherInfo .temperature,
    .widget .date{
        padding-bottom: 20px;
    }
    .widget .weatherInfo .description .weatherCondition{
        font-size: 20px;
    }
    .widget .weatherInfo .temperature,
    .widget .weatherInfo .description,
    .widget .date{
        font-size: 20px;
        padding: 20px;
        
    }
    .widget .extra-temp {
        grid-template-columns: 1fr;
        grid-gap:20px;
    }
}
@media (max-width:500px){
    .widget{
        width: 90%;
    }}
